export const processLog = (text: string, lang: Lang): string => {
    if (!text) return "";
    
    if (lang === 'en') {
        return text
            .replace("=== Quick Investigation ===", "üìú [Quick Report]")
            .replace("Escaped", "Escaped üö™")
            .replace("Looping", "Looping üîÑ")
            .replace("MADNESS", "MENTAL BREAKDOWN üíÄ");
    }

    // 1. Á≤æÁ¢∫Êü•Ë°® (ÂÑ™ÂÖà)
    if (LOG_MAP[text]) return LOG_MAP[text];

    // 2. Ê®°Á≥ä/ËÆäÊï∏ËôïÁêÜ
    let t = text;
    
    // ÁµêÂ±ÄËôïÁêÜ (Âõ†ÁÇ∫ÂêàÁ¥ÑÈÄô‰∫õÊòØ hardcoded ‰ΩÜÂèØËÉΩÊúÉÊ∑∑Âú®ÂÖ∂‰ªñ log Ë£°)
    if (t.includes("Result: Mind Shattered")) return "üíÄ ÁµêÊûúÔºöÂøÉÊô∫Á†¥Á¢é„ÄÇ";
    if (t.includes("Result: You found the exit")) return "üéâ ÁµêÊûúÔºö‰Ω†ÊâæÂà∞‰∫ÜÂá∫Âè£ÔºÅ";
    if (t.includes("It was a trap.")) return "üîÑ ÈÇ£ÊòØÈô∑Èò±„ÄÇ‰Ω†ÂèàÂõûÂà∞‰∫ÜÂéüÈªû„ÄÇ";
    
    // ÈÄöÁî®ÊõøÊèõ
    t = t.replace("Escaped", "ÊâìÂç°‰∏ãÁè≠")
         .replace("Looping", "ÁÑ°ÈôêÂä†Áè≠")
         .replace("MADNESS", "ÈÅéÂãûÊ≠ª(Á≤æÁ•ûÂ¥©ÊΩ∞)")
         .replace("Staff #", "Âì°Â∑•Á∑®Ëôü #");

    return t;
};